import Vue from 'vue';
import Component from 'vue-class-component';

@Component
export class TagHelper extends Vue {
  createTag() {
    const name = window.prompt('');
    if (name === null) {return;} // prompt  null
    if (name === '') {
      return window.alert('');
    } else {
      this.$store.commit('createTag', name);
    }
  }
}